*{
   padding: 0;
   margin: 0;
}

.container{
    padding: 0px 60px;
    /* max-width: 900px; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* border: 1px solid red; */
    margin: auto;
    font-family: "Maven Pro", sans-serif;
}

.flex{
    display: flex;
}

.header{
    align-items: center;
    justify-content: space-between;
}

img{
    /* border:2px solid red; */
    width: 200px;
}

.menu{
    justify-content: space-between;
    cursor: pointer;
}

.menu li{
    font-size: 1.2rem;
    margin: 0 30px;
    list-style: none;
    color: rgb(185 185 185);
}

.menu li:hover{
    color:#ffc200;
}

#contactLogo{
    /* border: 1px solid red; */
    width: 165px;
    display: flex;
    /* justify-content: space-between; */
    padding-bottom: 25px;
}

.scocialIcon{
    /* padding: 0 12px; */
    object-fit: contain;
    color: rgb(219, 219, 219);
    /* margin-left: 40px; */
}



/*
Banner Desing 
Start
*/

.leftBanner{
    margin-top: 60px;
    /* border:2px solid red; */
}


.leftBanner h5{
    color: #5b5b5b;
    font-size: 14px;
    padding: 8px 16px;
}

.leftBanner h1{
    margin:18px 0;
}

.leftBanner h1 span{
    color:#ffc200;
}

.typical-heading{
    color:#ffc200;
    font-size: 28px;
}

.leftBanner p{
    line-height: 25px;
    font-weight: 300;
    margin-bottom: 20px;
    font-family: 'Work Sans', sans-serif;
}

.rightBanner img{
    width:450px
}


button{
   padding: 18px 30px;
   margin-left: 9px;
   border-radius: 17px;
   font-weight: bold;
   cursor: pointer;
}

button.btn1{
    background: #eee;
    color: #5b5b5b;
    border:2px solid #eee;
}

button.btn2{
    background: #474559;
    color: #ffffff;
    border:2px solid #474559;
}



/*
Banner Desing 
End
*/


/*
Project Banner
Start
*/


.projects{
    margin-top: 95px;
}


.projects h1{
    text-align: center;
    font-family: 'Fira Sans', sans-serif;
    margin-bottom: 25px;
}

.projectBanner img{
    width: 100%;
}

/*
Project Banner
End
*/
.navLinks{
    text-decoration: none;
    color: gray;
}

.navLinks:hover{
    color: rgb(231, 177, 28);
}
.heading h3{
    /* text-decoration: underline; */
}

.heading .underline{
    color:#f5bd07;
    height: 4px;
    width: 90px;
}

.contact-container{
    display: flex;
    justify-content: space-around;
    margin: 40px 0px;
}

.margin-btw{
    margin-left: 10px;
}

.contact_details--container{
    /* border: 1px solid yellow; */
    box-sizing: border-box;
    padding-left:40px;
}

.contact_details--container .heading{
    margin-bottom: 32px;
}

.card-body{
    height: 300px;
    /* width: 600px */
}

input,textarea{
    border: 1px solid #f5bd07;
    outline: #f5bd07;
}

input:active{
    border: 1px solid #f5bd07;
}

.contact_Inform{
    color:rgb(182, 180, 180);
    margin-left: 10px;
    font-size: 15px;
}

.submitBtn:active{
    color:white;
}

.about-section{
    margin: 100px 0px;
}


.about-container{ 
    border: 1px solid rgb(214, 213, 213);
    margin: 40px 0px;
    border-radius: 6px;
    box-shadow: rgba(0, 0, 0, 0.45) 0px 5px 15px;
    padding: 20px 15px;
    background-color: rgb(255, 255, 255);
    /* background-color: red; */
}

.col h3{
    font-size: 18px;
}

.heading{
    font-family: 'IBM Plex Sans Thai Looped', sans-serif;
}


.about-contact{

}

.about-contact_item{
    list-style: none;
    text-decoration: none;
}

.about-contact_heading{
    color:#FFC107
}

.about_myskills{
    margin:15px 0px;
}


.about_myskills h3{
    text-align: center;
    color:#FFC107
}

strong{
    color:#d19f08
}

.skill_img{
    width: 50%;
}

.about_myskill__logoes{
    display: flex;
    margin: 10px 0px;
}

.about_myskill__logoes li{
    list-style: none;
}


